import { Directive, forwardRef, HostListener } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl, ValidationErrors } from '@angular/forms';
import { verifyMailFormat } from './compareEqual';

const COMPARE_EQUAL_VALIDATOR: any = {
  provide: NG_VALIDATORS,
  useExisting: forwardRef(() => ValidMailDirective),
  multi: true
};

@Directive({
  selector: '[aaValidMail]',
  providers: [COMPARE_EQUAL_VALIDATOR]
})
export class ValidMailDirective implements Validator {
  validate(control: AbstractControl): ValidationErrors {
    return verifyMailFormat()(control);
  }

  constructor() { }

}
