import { ValidatorFn, AbstractControl, Validators, ValidationErrors, RangeValueAccessor } from '@angular/forms';

const mailFormat = new RegExp('^[a-zA-Z0-9.-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$');

export function compareEqual(controlName: string): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (!control.parent || control.parent.get(controlName).value === control.value) {
      return null;
    } else {
      return { compareEqual: true };
    }
  };
}

export function verifyMailFormat(): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (mailFormat.test(control.value)) {
      return null;
    } else {
      return { emailFormat: true };
    }
  };
}
